export default {
  name: "home",
  components: {
    Modal
  },
  methods: {
      yes: function (event) {
        this.modals.modal = false;
        let data = {
            payment: this.modals.payment, 
            data_type: this.modals.data_type,
            privately: this.modals.privately,
            yes: true
        };
        var d = new Date();
        var t = d.getTime();
        firebase.database().ref("survey/" + t).set(data);
      },
      no: function (event) {
        this.modals.modal = false;
        let data = {
            payment: this.modals.payment, 
            data_type: this.modals.data_type,
            privately: this.modals.privately,
            yes: false
        };
        var d = new Date();
        var t = d.getTime();
        firebase.database().ref("survey/" + t).set(data);
      }
  },
  data() {
    let random = Math.random();
    let payment = ["for free", "for 1$", "for 10$", "for 20$", "for 30$", "for 40$", "for 50$", "for 60$", "for 70$", "for 80$", "for 90$", "for 100$", "for 1000$"][Math.round(random * 12)];
    let data_type = ["genetic data", "medical health records", "medical X-ray data", "sexual health data"][Math.round(random * 3)];
    let privately = ["to a secure system", ""][Math.round(random)];
    return {
      modals: {
        modal: true,
        payment: payment,
        data_type: data_type,
        privately: privately
      }
    };
  }
};
</script>
